import React, { useEffect,useState } from 'react'
import {Redirect,Link} from 'react-router-dom'
import axios from 'axios'
import {BASE_URL} from '../constants'
import styled from 'styled-components'
import { ControlResponseStatusCode } from 'nexmo'
import _ from 'lodash'

function status (val) {
    switch(val){
        case "0":
            return "  "
            break;
        case "1":
            return "   "
            break;
        case "2":
            return "  "
            break;
        case "3":
            return "  "
            break;
        case "4":
            return "  "
            break;
        case "5":
            return "  "
            break;
        case "6":
            return "  "
            break;
        case "7":
            return " "
            break;
    }
}

const master =[
    302600424,200043263,39806823,301724761,39211024,300329331,305065021,301023651,28073047,34233122,300168507,1112
    // ,206324592
]
const mahlakot = [
    ''
    ,' '
    ,''
    ,''
    ,''
    ,'"'
    ,' '
    ,''
    ,''
    ,' '
    ,' '
    ,''
    ,' '
    ,' '
    ,' '
]

const Stats = () => {

    const [loader,setLoader] = useState(true)
    const [redi,setRedi] = useState(false)
    const [type,setType] = useState("master")
    const [unit,setUnit] = useState("")
    const [select,setSelect] = useState(true)
    const [unitType,setUnitType] = useState(1)
    const [peop,setPeop] = useState([])

    useEffect(()=>{
        if(mahlakot.includes(unit)){
            setUnitType(0)
            axios
            .post(BASE_URL+'/api/getusersby2',{hanaga:unit})
            .then(res=>{
                console.log(res)
                let arr = _.sortBy(res.data.data, [function(o) { return o.name; }]);
                console.log(arr)
                setPeop(arr)
            })
        }
        else{
            setUnitType(1)
            axios
            .post(BASE_URL+'/api/getusersby',{hanaga:unit})
            .then(res=>{
                console.log(res)
                let arr = _.sortBy(res.data.data, [function(o) { return o.name; }]);
                console.log(arr)
                setPeop(arr)
            })
        }

       
    },[unit])

    useEffect(()=>{
        
        let x1 = JSON.parse(localStorage.getItem("userdata"))
        console.log(x1)
        if(master.includes(x1.id)){
            setType("master")
            setSelect(false)
            setUnit("")
            setLoader(false)
        }else{
            if(x1.role == "/ " || x1.role == "/  /" ){
                setType("user")
                setLoader(false)
                if(x1.role == "/ "){
                    setUnit(x1.bigunit)
                }else{
                    setUnit(x1.unit)
                }
            }
            else{
                setRedi(true)
                setLoader(false)
            }
        }

    },[])

    return (
        <React.Fragment>
            {loader ?
                 <div class="lds-ellipsis"><div></div><div></div><div></div><div></div></div>
                :
                <Statcon>
                {redi && <Redirect to="/"/>}
                <h1 >  </h1>
                <Divi>
                    <label>\</label>
                    <select 
                        onChange={(e)=>{
                            console.log(e.target.value)
                            setUnit(e.target.value)}}
                        disabled={select}
                        value={unit}
                    >
                        <option value={'0'}> /</option>
                        <option value={''}></option>
                        <option value={''}></option>
                        <option value={''}></option>
                        <option value={''}></option>
                        <option value={''}></option>
                        <option value={''}></option>
                        <option value={''}></option>
                        <option value={'" '}>" </option>
                        <option value={'"'}>"</option>
                        <option value={''}></option>
                        <option value={''}></option>
                        <option value={''}></option>
                        <option value={''}></option>
                        <option value={''}></option>
                        <option value={''}></option>
                        <option value={''}></option>
                        <option value={' '}> </option>
                        <option value={''}></option>
                        <option value={''}></option>
                        <option value={''}></option>
                        <option value={'"'}>"</option>
                        <option value={' '}> </option>
                        <option value={''}></option>
                        <option value={''}></option>
                        <option value={' '}> </option>
                        <option value={' '}> </option>
                        <option value={''}></option>
                        <option value={' '}> </option>
                        <option value={' '}> </option>
                        <option value={' '}> </option>
                    </select>
                </Divi>
                {
                    peop.length == 0 ? 
                    <div className="lds-ellipsis"><div></div><div></div><div></div><div></div></div>
                    :
                
                <Table>
                    <thead>
                        <th> </th>
                        <th></th>
                        <th> </th>
                        <th>  </th>
                    </thead>    
                    <tbody>
                        {
                        peop.map((x)=>{
                            return(
                                <tr>
                                    <td>{x.name} {x.lastname}</td>
                                    <td>{x.role}</td>
                                    <td><StatusCircle status={x.step}/>{status(x.step)}</td>
                                    <td>{x.step == 0 ? "-":(x.step == 7 ? <Link to={`/evaluate/${x._id}`}>   </Link>:"  ")}</td>
                                </tr>
                            )
                        })
                        }
                    </tbody>
                </Table>
                }
            </Statcon>          
        }
        </React.Fragment>

    )
}

export default Stats


const StatusCircle = styled.div`
    background:${props=> props.status == "0" ? "gray" : (props.status == "7" ? "green":"yellow")};
    width:9px;
    height:9px;
    display:inline-block;
    margin-left:10px;
    border-radius:25px; 
`

const Statcon = styled.div`

text-align:center;
    h1{
        text-align:center;
    }
    label{
        text-align:right;
    }
    
`
const Divi = styled.div`
    display:flex;
    flex-direction:column;
    width: 20%;
    justify-content:flex-start;
    margin-bottom:2rem;
    margin-right:10%;
    select{
        padding:.5rem;
        border-radius:5px;
    }

`

const Table = styled.table`
    width:80%;
    margin:auto;
    margin-bottom:3rem;
    tbody{
        box-shadow:0 0 0 1px #4f4f4f;
        border-radius:5px;
    }
    td{
        padding: 1rem .5rem;
    }
`